.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  background-color: rgba(51, 21, 223, 0.8);

}

.card {
    background: #fff;
    border-radius: 2px;
    height: 150px;
    margin: 1rem;
    position: relative;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:rgba(0, 0, 0, 0.02);
  }
  .card > .img-container > img {
    height: 100%
  }
  
#scoreJumbo {
    background-color: rgba(250, 250, 250, 0.8);
}

p {
    font-size: 2.5em;
}
#topJumbo{
    background-color: rgba(173, 51, 51, 0.829);
}
